export default {
    data() {
        return {
            show2: false
        }
    },
    props: {
        toggle: {
            type: Boolean,
            default: false
        },
        info: {
            type: Object,
            default: {
                img: '/static/img/temp/poster.jpg',
                type: 'mini',//mini:小程序，h5: H5
            }
        }
    },
    watch: {
        toggle() {
            this.show2 = !this.show2;
        }
    },
    methods: {
        stop() {},
        closeShade2: function() {
            this.show2 = false;
        },
        share(val) {
            this.$emit('click', {
                type: val,
                info: this.info
            })
            this.closeShade2();
        }
    }
}
